import React, { useState, useEffect } from 'react';
import { LogOutIcon, PillIcon, BarChartIcon, AlertTriangle, UserIcon } from 'lucide-react';
import { PatientList } from './PatientList';
import { PatientDetails } from './PatientDetails';
import { Statistics } from './Statistics';
import { PharmacyPatient, fetchPatientsWithPrescriptions, getPharmacyStats } from './pharmacyUtils';
import { useAuth } from '../context/AuthContext';
import DataFreshness from './DataFreshness';

// User interface for authentication context
interface User {
  fullName: string; // Changed from 'name' to 'fullName' to match User in mockPatients
  role: string;
  // Other user properties (optional)
  _id?: string;
  username?: string;
  email?: string;
}

interface DashboardProps {
  user: User | null; // Assuming user can be null if not logged in
  onLogout: () => void;
}

export const Dashboard = ({
  user,
  onLogout
}: DashboardProps) => {
  const [activeTab, setActiveTab] = useState('dispense'); // 'dispense' or 'statistics'
  const [selectedPatient, setSelectedPatient] = useState<PharmacyPatient | null>(null);
  const [waitingPatients, setWaitingPatients] = useState<PharmacyPatient[]>([]);
  const [stats, setStats] = useState<any>(null);
  
  // State cho loading và error
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  // Tải dữ liệu bệnh nhân chờ phát thuốc từ API
  const fetchPatients = async () => {
    setIsLoading(true);
    setError(null);
    
    try {
      console.log('Dashboard: Fetching patients with prescriptions...');
      // Use the new fetchPatientsWithPrescriptions function which prioritizes MongoDB data
      const patients = await fetchPatientsWithPrescriptions();
      
      if (!patients || patients.length === 0) {
        console.warn("No patients with pending prescriptions found");
        
        // Keep showing the previous data if we had some, otherwise show a message
        if (waitingPatients.length === 0) {
          setError("Không tìm thấy đơn thuốc nào chờ phát thuốc");
        }
      } else {
        console.log(`Dashboard: Found ${patients.length} patients with pending prescriptions`);
        setWaitingPatients(patients);
        
        // Reset selected patient if not in the new list
        if (selectedPatient && !patients.find(p => p._id === selectedPatient._id)) {
          setSelectedPatient(null);
        }
        
        setError(null);
      }
      
      // Also fetch statistics data
      const statsData = await getPharmacyStats();
      setStats(statsData);
    } catch (err) {
      console.error("Error fetching patients:", err);
      setError("Lỗi khi tải dữ liệu bệnh nhân");
    } finally {
      setIsLoading(false);
    }
  };
  
  // Fetch patients when component mounts
  useEffect(() => {
    fetchPatients();
    
    // Set up polling to refresh data every 45 seconds
    const pollingInterval = setInterval(() => {
      console.log('Polling for new pharmacy data...');
      fetchPatients();
    }, 45000);
    
    return () => clearInterval(pollingInterval);
  }, []);
  
  // Xử lý chọn một bệnh nhân
  const handlePatientSelect = (patient: PharmacyPatient) => {
    console.log('Selected patient:', patient);
    setSelectedPatient(patient);
  };
  
  // Xử lý hoàn thành việc cấp phát thuốc
  const handleDispenseComplete = (patientId: string) => {
    setWaitingPatients(prevPatients => prevPatients.filter(p => p._id !== patientId));
    setSelectedPatient(null);
  };
  
  // Xác định chức năng ứng dụng hiển thị dựa trên tab hiện tại
  const renderContent = () => {
    switch(activeTab) {
      case 'dispense':
        return (
          <div className="flex">
            {/* Danh sách bệnh nhân bên trái */}
            <div className="w-1/3 pr-4">
              <h3 className="text-lg font-semibold mb-4">Danh Sách Chờ Phát Thuốc</h3>
              <DataFreshness onRefresh={fetchPatients} />
              <PatientList 
                patients={waitingPatients}
                selectedPatientId={selectedPatient?._id}
                onSelectPatient={handlePatientSelect}
                isLoading={isLoading}
                error={error}
              />
            </div>
            
            {/* Thông tin chi tiết bên phải */}
            <div className="w-2/3">
              {selectedPatient ? (
                <PatientDetails 
                  patient={selectedPatient} 
                  onDispenseComplete={handleDispenseComplete}
                />
              ) : (
                <div className="h-full flex items-center justify-center bg-gray-50 border-2 border-dashed border-gray-200 rounded-lg">
                  <div className="text-center text-gray-500">
                    <AlertTriangle size={48} className="mx-auto mb-2" />
                    <p>Chọn bệnh nhân để xem thông tin chi tiết</p>
                  </div>
                </div>
              )}
            </div>
          </div>
        );
      case 'statistics':
        return <Statistics 
                 pendingCount={stats?.pendingPrescriptions || 0}
                 dispensedToday={stats?.dispensedToday || 0}
                 totalPatients={stats?.totalPatients || 0}
                 totalRevenue={stats?.totalRevenue || 0}
               />;
      default:
        return null;
    }
  };

  // Now render the component with our modifications
  return (
    <div className="h-screen flex flex-col bg-gray-100">
      {/* Header */}
      <header className="bg-white shadow-sm border-b border-gray-200">
        <div className="flex justify-between items-center px-6 py-4">
          <h1 className="text-xl font-semibold text-gray-800">Quản Lý Phát Thuốc</h1>
          <div className="flex items-center space-x-4">
            <div className="text-sm text-gray-600">
              {user ? (
                <span>Xin chào, <span className="font-medium">{user.fullName}</span></span>
              ) : (
                <span>Chưa đăng nhập</span>
              )}
            </div>
            <button 
              onClick={onLogout}
              className="p-2 rounded-full hover:bg-gray-100 text-gray-500"
            >
              <LogOutIcon size={18} />
            </button>
          </div>
        </div>
        
        {/* Tab navigation */}
        <div className="flex px-6">
          <button
            className={`py-3 px-4 ${activeTab === 'dispense' ? 'border-b-2 border-blue-500 text-blue-600' : 'text-gray-600'} font-medium`}
            onClick={() => setActiveTab('dispense')}
          >
            <div className="flex items-center space-x-2">
              <PillIcon size={16} />
              <span>Phát Thuốc</span>
            </div>
          </button>
          
          <button
            className={`py-3 px-4 ${activeTab === 'statistics' ? 'border-b-2 border-blue-500 text-blue-600' : 'text-gray-600'} font-medium`}
            onClick={() => setActiveTab('statistics')}
          >
            <div className="flex items-center space-x-2">
              <BarChartIcon size={16} />
              <span>Thống Kê</span>
            </div>
          </button>
        </div>
      </header>
      
      {/* Main content */}
      <main className="flex-1 overflow-y-auto p-6">
        {activeTab === 'dispense' ? 
          <div className="flex">
            {/* Danh sách bệnh nhân bên trái */}
            <div className="w-1/3 pr-4">
              <h3 className="text-lg font-semibold mb-4">Danh Sách Chờ Phát Thuốc</h3>
              <DataFreshness onRefresh={fetchPatients} />
              <PatientList 
                patients={waitingPatients}
                selectedPatientId={selectedPatient?._id}
                onSelectPatient={handlePatientSelect}
                isLoading={isLoading}
                error={error}
                onRefresh={fetchPatients}
              />
            </div>
            
            {/* Thông tin chi tiết bên phải */}
            <div className="w-2/3">
              {selectedPatient ? (
                <PatientDetails 
                  patient={selectedPatient} 
                  onDispenseComplete={handleDispenseComplete}
                />
              ) : (
                <div className="h-full flex items-center justify-center bg-gray-50 border-2 border-dashed border-gray-200 rounded-lg">
                  <div className="text-center text-gray-500">
                    <AlertTriangle size={48} className="mx-auto mb-2" />
                    <p>Chọn bệnh nhân để xem thông tin chi tiết</p>
                  </div>
                </div>
              )}
            </div>
          </div> : 
          <Statistics 
            pendingCount={stats?.pendingPrescriptions || 0}
            dispensedToday={stats?.dispensedToday || 0}
            totalPatients={stats?.totalPatients || 0}
            totalRevenue={stats?.totalRevenue || 0}
          />
        }
      </main>
    </div>
  );
};